.about-us {
  margin-bottom: 100px;
}
.about-us .container .about-us_title {
  margin-bottom: 50px;
}
.about-us .container .about-us_title h3 {
  position: relative;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 2.30625px;
}
.about-us .container .about-us_title h3:after {
  position: absolute;
  content: "";
  display: block;
  width: 50px;
  left: calc(50% - 25px);
  bottom: -15px;
  border-bottom: 3px solid #00A4E5;
}
.about-us .container .about-us_decription {
  display: flex;
  justify-content: space-between;
}
.about-us .container .about-us_decription .description__text {
  width: 400px;
  margin-right: 10px;
}
.about-us .container .about-us_decription .description__text .description__text-title {
  margin-bottom: 30px;
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  align-items: flex-end;
  letter-spacing: 0.1875px;
}
.about-us .container .about-us_decription .description__text .description__text-body {
  font-size: 13px;
  line-height: 23px;
  align-items: center;
  letter-spacing: 0.1875px;
}
.about-us .container .about-us_decription .description__images {
  display: flex;
  gap: 20px;
  width: 50%;
}
.about-us .container .about-us_decription .description__images .description__images-container {
  position: relative;
  width: 33.3%;
  height: 224px;
}
.about-us .container .about-us_decription .description__images .description__images-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-us .container .about-us_decription .description__images .description__images-container .description__images-text {
  position: absolute;
  top: 20px;
  left: 10px;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff;
  align-items: center;
  letter-spacing: 0.1875px;
}